import React, { useEffect } from 'react';
import { navigate } from 'gatsby';

const IndexPage = ({ pageContext }) => {

    const { lastItem } = pageContext;

    useEffect(() => {
        if (lastItem && lastItem.id) {
            navigate(`/${lastItem.id}`);
        }
    }, [lastItem]);

    return (
        <div>
            <h1>Hello World</h1>
        </div>
    )
}

export default IndexPage;